<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-button type="primary" class="btn" @click="addCate">添加分类</el-button>
      <tree-table :data="cateList" :columns="columns" show-index :selection-type="false" :expand-type="false">
        <template slot="isok" scope="scopes">
          <span class="el-icon-success" v-if="scopes.row.cat_deleted === false" style="color:green"></span>
          <span class="el-icon-error" v-else style="color:red"></span>
        </template>
        <template slot="order" scope="scopes">
          <el-tag v-if="scopes.row.cat_level === 0">一级</el-tag>
          <el-tag v-else-if="scopes.row.cat_level === 1" type="warning">二级</el-tag>
          <el-tag v-else type="danger">三级</el-tag>
        </template>
        <template slot="cz" scope="scopes">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="edit(scopes.row.cat_id)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="delCate(scopes.row.cat_id)">删除</el-button>
        </template>
      </tree-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="cate.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="cate.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加分类 -->
    <el-dialog title="添加分类" :visible.sync="adddialogVisible" width="50%" @close="closeDialog">
      <el-form ref="addCateForm" :model="addCateForm" :rules="addCateRules" label-width="80px">
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="addCateForm.name"></el-input>
        </el-form-item>
        <el-form-item label="分类父级">
          <el-cascader :options="catesec" :props="prop" v-model="selectedOptions" @change="handleChange" change-on-select> </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureAddCate">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑分类 -->
    <el-dialog title="编辑分类" :visible.sync="editdialogVisible" width="50%" @close="closeEditDialog">
      <el-form ref="editCateForm" :model="editCateForm" :rules="editCateRules" label-width="80px">
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="editCateForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureeditCate()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cate: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      cateList: [],
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          type: 'template',
          template: 'isok'
        },
        {
          label: '排序',
          type: 'template',
          template: 'order'
        },
        {
          label: '操作',
          type: 'template',
          template: 'cz'
        }
      ],
      total: 0,
      adddialogVisible: false,
      editdialogVisible: false,
      selectedOptions: '',
      catesec: [],
      edit_Id: '',
      prop: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      addCateForm: {
        name: ''
      },
      editCateForm: {
        name: ''
      },
      addCateRules: {
        name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      },
      editCateRules: {
        name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getCate()
  },
  methods: {
    async getCate() {
      let { data: res } = await this.$http.get('categories', { params: this.cate })
      this.cateList = res.data.result
      this.total = res.data.total
      console.log(res.data)
    },
    handleSizeChange(newSize) {
      this.cate.pagesize = newSize
      this.getCate()
    },
    handleCurrentChange(newCurrent) {
      this.cate.pagenum = newCurrent
      this.getCate()
    },
    addCate() {
      this.adddialogVisible = true
      this.geterCate()
    },
    //获取选择的选项
    handleChange() {
      console.log(this.selectedOptions)
    },
    async geterCate() {
      let { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      this.catesec = res.data
      console.log(res.data)
    },
    //关闭添加窗口
    closeDialog() {
      this.$refs.addCateForm.resetFields()
      this.selectedOptions = []
    },
    sureAddCate() {
      this.$refs.addCateForm.validate(async valid => {
        if (!valid) return
        console.log(this.cat_level)
        console.log(this.cataId)

        let { data: res } = await this.$http.post('categories', {
          cat_pid: this.cataId,
          cat_name: this.addCateForm.name,
          cat_level: this.cat_level
        })
        this.adddialogVisible = false
        this.getCate()
      })
    },
    async delCate(id) {
      let result = await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      console.log(result)
      if (result !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      let { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除分类失败')
      }
      this.$message.success('删除成功')
      this.getCate()
    },
    sureeditCate() {
      this.$refs.editCateForm.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$http.put(`categories/${this.edit_Id}`, {
          cat_name: this.editCateForm.name
        })
        if (res.meta.status !== 200) {
          return this.$message.error('修改分类失败')
        }
        this.$message.success('修改成功')
        this.editdialogVisible = false
        this.getCate()
      })
    },
    async edit(id) {
      this.edit_Id = id
      this.editdialogVisible = true
      let { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取该分类失败')
      }
      this.editCateForm.name = res.data.cat_name
    },
    closeEditDialog() {
      this.$refs.editCateForm.resetFields()
    }
  },
  computed: {
    cat_level() {
      if (this.selectedOptions.length === 0) {
        return 0
      } else if (this.selectedOptions.length === 1) {
        return 1
      } else {
        return 2
      }
    },
    cataId() {
      if (this.selectedOptions.length === 0) {
        return 0
      } else if (this.selectedOptions.length === 1) {
        return this.selectedOptions[0]
      } else {
        return this.selectedOptions[1]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.btn {
  margin-bottom: 10px;
}
.el-cascader {
  width: 100%;
}
</style>
