<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="只允许修改三级分类设置" type="warning" :closable="false" show-icon> </el-alert>
      <el-row style="margin:20px 0;">
        <el-col>
          <span>选择商品列表</span>
          <el-cascader v-model="select" :options="cate" :props="options" @change="handleChange"></el-cascader>
        </el-col>
      </el-row>
      <!-- Tabs 标签页 -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" :disabled="disabled" @click="addDialogVisible = true">添加参数</el-button>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" :disabled="disabled" @click="addDialogVisible = true">添加属性</el-button>
        </el-tab-pane>
      </el-tabs>
      <el-table :data="activeName === 'many' ? manylist : onlylist" style="width: 100%" stripe border>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{ item }}</el-tag>
            <el-input
              class="input-new-tag"
              v-if="scope.row.inputVisible"
              v-model="scope.row.inputValue"
              ref="saveTagInput"
              size="small"
              @keyup.enter.native="handleInputConfirm(scope.row)"
              @blur="handleInputConfirm(scope.row)"
            >
            </el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
          </template>
        </el-table-column>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="attr_name" :label="activeName === 'many' ? '参数名称' : '静态属性'"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="edits(scope.row.attr_id)">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="del(scope.row.attr_id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加参数 和添加属性 -->
    <el-dialog :title="'添加' + titleText" :visible.sync="addDialogVisible" width="50%" @close="closeAdd">
      <el-form :model="addFrom" :rules="addrules" ref="addForm" label-width="15%">
        <el-form-item :label="titleText" prop="name">
          <el-input v-model="addFrom.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="add">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑参数和属性 -->
    <el-dialog :title="'编辑' + titleText" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editFrom" :rules="editrules" ref="editForm" label-width="15%">
        <el-form-item :label="titleText" prop="name">
          <el-input v-model="editFrom.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="edit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      attrid: '',
      cate: [],
      options: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      //获取选择的分类
      select: [],
      activeName: 'many',
      addDialogVisible: false,
      manylist: [],
      onlylist: [],
      addFrom: {
        name: ''
      },
      addrules: {
        name: [{ required: true, message: '不能空', trigger: 'blur' }]
      },
      editDialogVisible: false,
      editFrom: {
        name: ''
      },
      editrules: {
        name: [{ required: true, message: '不能空', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.get()
  },
  methods: {
    async get() {
      let { data: res } = await this.$http.get('categories')
      if (res.meta.status != 200) {
        return this.$message.error('获取商品分类数据列表失败')
      }
      this.cate = res.data
      // console.log(this.cate)
    },
    async handleChange() {
      if (this.select.length != 3) {
        return (this.select = [])
      }
      let { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      console.log(res.data)
      if (res.meta.status !== 200) return

      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        this.manylist = res.data
      } else {
        this.onlylist = res.data
      }
    },
    handleClick() {
      this.handleChange()
    },
    closeAdd() {
      this.$refs.addForm.resetFields()
      name = []
    },
    //    添加静态属性或者动态参数
    add() {
      //   console.log(1)
      //   console.log(this.cateId)
      //   console.log(this.activeName)
      //   console.log(this.addFrom.name)
      this.$refs.addForm.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addFrom.name,
          attr_sel: this.activeName
        })
        // console.log(res)
        if (res.meta.status !== 201) return

        this.addDialogVisible = false
        this.handleChange()
      })
    },
    async del(attrid) {
      let result = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (result !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      let { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${attrid}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      this.handleChange()
    },
    edit() {
      this.$refs.editForm.validate(async valid => {
        console.log(this.activeName)
        if (!valid) return
        let { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.attrid}`, { attr_name: this.editFrom.name, attr_sel: this.activeName })
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error('修改失败')
        }
        this.editDialogVisible = false
        this.handleChange()
      })
    },
    async edits(attrId) {
      this.attrid = attrId
      let { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${attrId}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      this.editFrom.name = res.data.attr_name
      this.editDialogVisible = true
    },
    //删除详细的参数
    async handleClose(index, row) {
      console.log(row)
      console.log(index)
      row.attr_vals.splice(index, 1)
      let { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
    },
    // 确认输入
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false

      let { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('添加失败')
      }
      this.$message.success('添加成功')
    },
    // 点击tab显示输入框
    showInput(row) {
      // console.log(row.inputVisible)
      row.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    disabled() {
      if (this.select.length !== 3) {
        return true
      }
      return false
    },
    cateId() {
      if (this.select.length === 3) return this.select[2]
    },
    titleText() {
      if (this.activeName === 'many') {
        return '动态参数'
      }
      return '静态属性'
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 0 5px;
}
.input-new-tag {
  width: 100px;
}
</style>
